<!--
    /**
     * 
     * @author wz
     * @date 2020-06-09
     * 
     * <tree-select :height="400" // 
     *              :width="200" //  el-select input
     *              size="small"  // : medium/small/mini
     *              :data="data" // 
                    :obj="{}"    //
     *              multiple   // 
                    //||
                    :default-key="..."
     *              clearable   // 
     *              collapseTags   // 
                    expand-click-node   //
                    check-click-node //  
     *              checkStrictly //  elementui 
     *              @getValue=""> // ID
                    :default-expanded-keys="defaultKeys"
     *              </tree-select>
      <select-tree
     obj 
                        id:'id',//
                        label: 'name',// 
                        children: 'children', //
                        path:'path',//
                        content:'content',//
                        pid:'pid',//id

     */
-->
<template>
  <div>
    <div class="mask" v-show="isShowSelect"></div>
    <el-popover placement="bottom-start"  :width="popoverWidth"    trigger="manual"  v-model="isShowSelect" @hide="popoverHide">
      <el-tree class="common-tree" :width="width"   ref="tree" :data="treeData" :props="obj"
               :show-checkbox="multiple"
               :node-key="obj.id"
               :check-strictly="checkStrictly"
               :expand-on-click-node="multiple&&expandClickNode"
               :check-on-click-node="checkClickNode"
               :highlight-current="true"
               @check-change="nodeClick"
               @node-click="nodeClick"
      ></el-tree>
      <el-select slot="reference" ref="select" :size="size"
                 v-model="returnDataKeys"
                 :multiple="multiple"
                 :clearable="clearable"
                 :collapse-tags="collapseTags"
                 @click.native="selectClick"
                 @remove-tag="removeTag"
                 @clear="clean"
                 class="tree-select">
        <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>
      <el-row>
        <el-button v-if="multiple" class="ok" @click="isShowSelect=false" size="mini" type="text"></el-button>
      </el-row>
    </el-popover>
  </div>
</template>

<script>
export default {
  name: 'test-code',
  props: {
    // 
    data: {
      type: Array,
      default () {
        return [];
      },
    },
    obj: {
      type: Object,
      required: false,
      default:()=>{
        return {
          id:'name',// ID
          label: 'name',// 
          children: 'children', //
          //path:'path',//
          //content:'content',//
          pid:'pid',//id
        }
      }
    },
    // 
    multiple: {
      type: Boolean,
      default () {
        return false;
      }
    },
    // 
    clearable: {
      type: Boolean,
      default () {
        return false;
      }
    },
    // 
    collapseTags: {
      type: Boolean,
      default () {
        return false;
      }
    },
    // 
    checkStrictly: {
      type: Boolean,
      default () {
        return false;
      }
    },
    //
    checkClickNode:{
      type: Boolean,
      default () {
        return false;
      }
    },
    //
    expandClickNode:{
      type: Boolean,
      default () {
        return false;
      }
    },
    // key
    defaultKey: {
      type: [Number,String,Array,Object],
      default () {
        return [];
      }
    },
    size: {
      type: String,
      default () {
        return 'small';
      }
    },
    width: {
      type: String,
      default () {
        return '100%';
      }
    },
    height: {
      type: String,
      default () {
        return '300px';
      }
    },
    modelId: String
  },
  //
  data () {
    return {
      popoverWidth:"0px",//
      isShowSelect: false, // 
      options: [],//select option
      returnDatas:[],//
      returnDataKeys:[],//
    };
  },
  computed: {
    treeData() { // 
      return   JSON.stringify(this.data).indexOf(this.obj.children) !== -1 ? this.data :  this.switchTree();
    },
  },
  methods: {
    init(){
      // eslint-disable-next-line no-undef,no-debugger
      // debugger
      if(this.defaultKey!=undefined&&this.defaultKey.length>0){
        if (this.multiple) {
          // 
          if( Object.prototype.toString.call(this.defaultKey).indexOf("Array")!=-1){
            if(Object.prototype.toString.call(this.defaultKey[0]).indexOf("Object")!=-1){//
              this. setDatas(this.defaultKey);
            }else if(Object.prototype.toString.call(this.defaultKey[0]).indexOf("Number")!=-1
                || Object.prototype.toString.call(this.defaultKey[0]).indexOf("String")!=-1){
              this.setKeys(this.defaultKey);
            }else{
              console.log("");
              return;
            }
          }else {
            console.log("");
            return;
          }

        } else {
          // 
          if( Object.prototype.toString.call(this.defaultKey).indexOf("Number")!=-1
              || Object.prototype.toString.call(this.defaultKey).indexOf("String")!=-1
              || Object.prototype.toString.call(this.defaultKey).indexOf("Object")!=-1 ){
            this.setKey(this.defaultKey);
          }else {
            console.log("");
            return;
          }
        }
      }
    },
    //select[]
    selectClick(){
      this.$nextTick(function () {//
        this.popoverWidth= this.$refs.select.$el.clientWidth-26;
      })
      //
      return this.isShowSelect = !this.isShowSelect
    },
    //: 
    nodeClick (data, node) {
      if (!this.multiple) {//
        this.isShowSelect=false;
        this.setKey(node.key);
      }else{//
        var checkedKeys = this.$refs.tree.getCheckedKeys(); //  key 
        var t=[];
        this.options = checkedKeys.map((item) => {//option
          var node = this.$refs.tree.getNode(item); // node
          t.push(node.data);
          return {label:node.label,value:node.key};
        });
        this.returnDataKeys = this.options.map((item) => {
          return item.value;
        });
        this.returnDatas=t;
      }
    },
    //:
    clean () {
      this.$refs.tree.setCurrentKey(null);//key
      this.returnDatas=null;this.returnDataKeys='';
      this.popoverHide ();

    },
    //: key
    setKey (thisKey) {
      this.$refs.tree.setCurrentKey(thisKey);
      var node = this.$refs.tree.getNode(thisKey);
      this.setData(node.data);
    },
    //
    setData(data){
      this.options = [];
      this.options.push({label:data[this.obj.label],value:data[this.obj.id]});
      this.returnDatas = data;
      this.returnDataKeys=data[this.obj.id]

    },
    //: keys
    setKeys (thisKeys) {
      this.$refs.tree.setCheckedKeys(thisKeys);
      this.returnDataKeys=thisKeys;
      var t=[];
      thisKeys.map((item) => {//option
        var node = this.$refs.tree.getNode(item); // node
        t.push(node.data);
        return {label:node.label,value:node.key};
      });
      this.returnDatas=t;
      this.popoverHide()
    },
    //:
    setDatas(data){
      this.$refs.tree.setCheckedNodes(data);
      this.returnDatas=data;
      var t=[];
      data.map((item) => {//option
        t.push(item[this.obj.id]);
      });
      this.returnDataKeys=t;
      this.popoverHide()
    },
    // ,select
    removeTag (val) {
      this.$refs.tree.setChecked(val, false);//
      var node = this.$refs.tree.getNode(val);//
      if (!this.checkStrictly && node.childNodes.length > 0) {
        this.treeToList(node).map(item => {
          if (item.childNodes.length <= 0) {
            this.$refs.tree.setChecked(item, false);
          }
        });
      }
      this.nodeClick();
      this.popoverHide ();

    },
    //
    popoverHide () {
      this.$emit('getValue', this.returnDataKeys, this.returnDatas);
    },
    // 
    clearSelectedNodes () {
      var checkedKeys = this.$refs.tree.getCheckedKeys(); //  key 
      for (let i = 0; i < checkedKeys.length; i++) {
        this.$refs.tree.setChecked(checkedKeys[i], false);
      }
    },

    //
    treeToList (tree) {
      var queen = [];
      var out = [];
      queen = queen.concat(tree);
      while (queen.length) {
        var first = queen.shift();
        if (first.childNodes) {
          queen = queen.concat(first.childNodes);
        }
        out.push(first);
      }
      return out;
    },
    switchTree() {
      return this.buildTree(this.data, this.defaultValue);
    },
    // 
    buildTree(data, id) {
      const fa = (id) => {
        const temp = [];
        for (let i = 0; i < data.length; i++) {
          const n = data[i];
          if (n[this.obj.pid] === id) {
            n[this.obj.children] = fa(n[this.obj.id]);
            temp.push(n);
          }
        }
        return temp;
      };
      return fa(id);
    },

  },
  watch: {
    // eslint-disable-next-line no-unused-vars
    isShowSelect (val) {
      // select
      this.$refs.select.blur();
    },
    treeData(){//tree
      this.$nextTick(()=> {
        this.init();
      })
    },
    modelId: {
      handler(newName, oldName) {
        debugger
        let self = this;
        if (newName) {
          this.$nextTick(() => {
            this.$refs.tree.setCheckedKeys([newName]);
            if (this.treeData.length > 0) {
              let data = this.treeData.filter((item) => {
                return item.id == newName
              });
              if (data.length > 0) {
                this.checkedId = data[0].id;
                this.dataTreeNode = data[0].name
              }
            }
          })
        } else {
          this.$nextTick(() => {
            this.checkedId = "";
            this.dataTreeNode = "";
            self.$refs.tree.setCheckedKeys([])
          })
        }
      },
      immediate: true
    }
  }
};
</script>

<style scoped>
  .mask{
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    opacity: 0;
    z-index: 11;
  }
  .common-tree{
    overflow: auto;
  }
  .tree-select{
    z-index: 111;
  }
  .ok{
    float: right;
  }
  .el-row{
    padding-top: 0px !important;
  }
</style>


